body {
  font-weight: 150;
  background-color: #f6f6f6;
  color:#047045;
  text-decoration: none;
}
.link{
  text-decoration: none;
  color: #f6f6f6;
  font-size: 30px;
}

h1, h2, h3, h4 {
  font-weight: 100;
  margin: 20px 0;
}
button{
  background-color:#047045 ;
  outline-color: #047045;
  color: #f6f6f6;
  border-radius: 6px;
  border: 3px solid #047045;
  padding: 7px;
}
button:hover{
  background-color:#444;
  color: #f6f6f6;
}

section {
  margin: 50px 0;
  background-color: #222;
  border-radius: 10px;
  
}
img{
  width: 100%;
  object-fit: cover;
  border-radius: 50%;
  height: 330px;
}
.home{
  padding: 5px 0px;
  box-shadow: 0 0 10px 0 #111;
 
}
.greyBack{
  background-color: #222;

}
.h1{
  font-size:70px;
  font-weight: 200;
  margin-top: 100px;
  text-align: center;
}
.display{
  display: flex;
}

.coaster-card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.coaster-card {
  padding: 0 20px;
  margin: 20px 0
}

.coaster-card h3 {
  font-size: 1.2em;
  margin: 5px 0;
}

.coaster-card p {
  font-size: .8em;
  margin-bottom: 0;
}


.coaster-card small {
  font-size: .7em;
}


.coaster-details img {
 width: 100% 
}

 .navbar-nav a {
    color: inherit;
}
.img{
  width: 100%;
}
.nav{
    background-color: #047045;
  box-shadow: 0 0 10px 0 #111;
    color: #f6f6f6;
}

